html {
  scroll-behavior: smooth;
  margin: 0;
  overflow-x: hidden;
}

body {  
  background-image: url(/portfolio-website/static/media/background.9d1f933b.jpg);
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-x: hidden;
  margin: 0;
}

.footer-container {
    position: absolute;
    bottom: 0;
    font-family: 'Secular One', sans-serif;
    width: 100%;
    text-align: center;
    font-size: 16px;
    letter-spacing: 3px;
    background: black;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer-text {
    margin-top: 10px;
    margin-bottom: 10px;
}

.footer-media-icons {
    padding: 10px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.footer-icon {
    width: 25px;
    vertical-align: middle;
}
#aboutsection {
    height: 100vh;
    position: relative;
    z-index: 1;
}

.about-container {
    display: flex;
    height: 100%;  
    width: 100%;
    font-family: 'Rubik', sans-serif; 
}

.about-information {
    background: white;
}

.about-me-title {
    height: 50px;
    width: 100%;
    border-bottom: 1px solid white;
    background: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
}

.me-text {
    color: #c96464;
}

.about-me-title-text {
    font-size: 30px;
    letter-spacing: 1px;
}

.about-me-information-container {
    padding: 20px;    
    display: flex;
    flex-direction: column;
    height: 85%;
    justify-content: space-between;
}

.about-me-information {
   margin-top: 0;
}

.about-me-information-one-text {
    margin-top: 20px;
}

#aboutinfo3 p {
    margin: 5px;
}

.socialtitle {
    color: red;
    margin-right: 5px;
}

.about-contact-me-title {
    display: flex;
    align-items: center;
    text-align: center;
    font-style: italic;
    font-size: 18px;
}

.about-contact-me-title:before,
.about-contact-me-title:after {
  background: black;
  height: 1px;
  flex: 1 1;
  content: '';
}

.about-contact-me-title-text {
    padding-right: 10px;
    padding-left: 10px;
}

.about-me-information-one {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    text-align: left;
}

.about-me-photo {
    width: 100%;
    height: auto;
}

#thank-you-text {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 0;
    letter-spacing: 1px;
}

.about-me-signature {
    height: auto;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
}

@media only screen and (min-width: 768px) {
    #aboutsection {
        display: flex;
        justify-content: center;
    }
    .about-container {
        width: 80%;
        height: 80%;
        border-radius: 7px;
    }

    .about-me-photo {
        width: 50%;
        align-self: center;
    }

    .about-me-information-one-text {
        margin-top: 50px;
        font-size: 30px;
    }

    .footer-media-icons {
        padding: 30px;
    }

    .footer-icon {
        width: 40px;
    }

    .about-me-title {
        height: 80px;
    }

    .about-me-title-text {
        font-size: 60px;
    }

    .about-information {
        border-radius: 7px;
    }
    .about-me-information-container {
        height: 80%;
    }
}

@media only screen and (min-width : 992px) {
    #aboutsection {
        height: 110vh;
    }
    .about-container {
        width: 50%;
    }
    .footer-media-icons {
        padding: 10px;
    }
    .about-me-information-container {
        height: 80%;
    }
    .about-container {
        height: 100%;
    }
    #thank-you-text {
        font-size: 30px;
    }
    .about-me-signature {
        width: 70%;
    }
    .about-me-information-three {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
#herosection {
    height: 100vh;
    position: relative;
    z-index: 1;
    margin-bottom: 50vh;
}

h1, h4 {
    margin: 0;
}

.hero-container {
    position: absolute;
    top: 50%; left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: 'Roboto Slab', serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 70%;
    justify-content: space-between;
}

.hero-title {
    color: black;
    text-align: center;
    margin: 0;
    font-size: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    animation: fadeIn linear 1s;
    -webkit-animation: fadeIn linear 1s;
    -moz-animation: fadeIn linear 1s;
    -o-animation: fadeIn linear 1s;
    -ms-animation: fadeIn linear 1s;
    letter-spacing: 1px;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

.hero-hi {
    color: #4d4d4d;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;    
    animation: fadeIn linear 1s;
    -webkit-animation: fadeIn linear 1s;
    -moz-animation: fadeIn linear 1s;
    -o-animation: fadeIn linear 1s;
    -ms-animation: fadeIn linear 1s;
}

.hero-motto {
    font-size: 30px;
    text-align: center;
}

.hero-icons-container {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.hero-icon {
    height: 60px;
    width: auto;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
}

.hero-icon:hover {
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.break-one {
    display: none;
}

.break-two {
  margin: 15px 0px 15px;
  height: 1px;
  background: black;
  width: 30%;
  border: none;
}

.hero-subsection {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media only screen and (min-width: 768px) {
  .hero-container {
    height: 60%;
  }

  .hero-title {
    font-size: 7vh;
  }

  .hero-motto {
    font-size: 45px;
  }

  .hero-icon {
    height: 80px;
  }
}

@media only screen and (min-width : 992px) {
  .hero-container {
    height: 40%;
  }

  .hero-title {
    font-size: 10vh;
  }

  .hero-motto {
    font-size: 7vh;
  }

  .break-two {
    height: 3px;
  }
}

